@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
:root {
  font-size: 62.5%; }

.btn {
  background-color: #E4C1AE;
  min-width: 13.5rem;
  min-height: 4.7rem;
  border-radius: 4.4rem;
  -webkit-border-radius: 4.4rem;
  -moz-border-radius: 4.4rem;
  -ms-border-radius: 4.4rem;
  -o-border-radius: 4.4rem;
  -webkit-border-radius: 4.8rem;
  font-size: 1.6rem;
  font-weight: 700;
  color: #FFFFFF; }
  .btn:hover {
    background-color: #e6a17c; }

/* variables */
* {
  text-decoration: none;
  outline: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

/* Fonts properties */
h1,
p,
a {
  font-family: 'Roboto', sans-serif; }

/* Layout properties */
body {
  background-color: #F0F0F0; }

[class*="con"] {
  background-color: #FFFFFF;
  color: #121212;
  margin-top: 6.25rem; }

/* -- Welcome section -- */
.welcome [class*="col"] {
  padding: 0;
  /* min-height: 40.7rem; */ }

.welcome img {
  /* object-fit: cover; */
  width: 100%;
  /* height: auto; */ }

.welcome .text-block {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 4.3rem 5.05rem 3.45rem; }
  .welcome .text-block h1 {
    font-size: 4.2rem;
    font-weight: 700; }
  .welcome .text-block p {
    font-size: 1.6rem;
    font-weight: 300;
    margin: 4.3rem 0 3.5rem; }
  .welcome .text-block .btn {
    align-self: flex-end; }

@media screen and (min-width: 600px) {
  /* -- Welcome section -- */
  .welcome img {
    height: 100%; } }

@media screen and (min-width: 600px) and (min-width: 1024px) {
  /* -- Welcome section -- */
  .welcome img {
    height: 100%; }
  .welcome .text-block {
    padding: 8.6rem 10.1rem 6.9rem; } }
