/* variables */

* {
    text-decoration: none;
    outline: none;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}


/* Fonts properties */

h1,
p,
a {
    font-family: 'Roboto', sans-serif;
}


/* Layout properties */

body {
    background-color: $bgColor;
}

[class*="col"] {}

[class*="con"] {
    background-color: $secondaryColor;
    color: $textColor;
    margin-top: 6.25rem;
}


/* -- Welcome section -- */

.welcome {
    [class*="col"] {
        padding: 0;
        /* min-height: 40.7rem; */
    }
    img {
        /* object-fit: cover; */
        width: 100%;
        /* height: auto; */
    }
    .text-block {
        height: 100%;
        display: flex;
        flex-direction: column;
        padding: ($topPadd * 0.5) ($horizonPadd * 0.5) ($bottPadd * 0.5);
        h1 {
            font-size: 4.2rem;
            font-weight: 700;
        }
        p {
            font-size: 1.6rem;
            font-weight: 300;
            margin: 4.3rem 0 3.5rem;
        }
        .btn {
            align-self: flex-end;
        }
    }
}

@media screen and (min-width: 600px) {
    /* -- Welcome section -- */
    .welcome {
        [class*="col"] {}
        img {
            height: 100%;
        }
        .text-block {
            h1 {}
            p {}
            .btn {}
        }
    }
    @media screen and (min-width: 1024px) {
        /* -- Welcome section -- */
        .welcome {
            [class*="col"] {}
            img {
                height: 100%;
            }
            .text-block {
                padding: 8.6rem 10.1rem 6.9rem;
                h1 {}
                p {}
                .btn {}
            }
        }
    }
}